  getAssets = () => {
    const user = JSON.parse(localStorage.getItem("logInUserData"));
    const { assetList, liabilityList } = this.state;
    //const assetList = [];
    assetList.splice(0, assetList.length);
    liabilityList.splice(0, liabilityList.length);

    user.assets.forEach((e, i) => {
      console.log("ffffffffffffffff", i, e);
      let nomin = "";
      e.nominees.forEach((n, idx) => {
        if (idx === e.nominees.length - 1) {
          nomin = nomin + n.name;
        } else {
          nomin = nomin + n.name + ", ";
        }
      });
      const cont = (
        <div>
          <Row>
            {e.assetDetails.map((x) => {
              if (x.val !== "") {
                console.log("dddddddddddddddddd", x.key, this.state.assetShow);
                return (
                  <Col md="6" sm="12">
                    <FormText>
                      <b>{x.key} :</b>{" "}
                      {this.state.assetShow ? x.val : "***********"}
                    </FormText>
                  </Col>
                );
              }
            })}
            <Col md="6" sm="12">
              <FormText>
                <b>Nominees :</b>{" "}
                {this.state.assetShow ? nomin : "*******************"}
              </FormText>
            </Col>
          </Row>
        </div>
      );
      const ast = {
        id: i + 1,
        title: e.assetType,
        content: cont,
      };
      assetList.push(ast);
    });

    
    this.setState({ assetList: assetList});
  };
  static getDerivedStateFromProps(props, state) {
    if (props.dataList.data.length !== state.nomineeList.length) {
      const nomineeOption = state.nomineeOption;
      props.dataList.data.forEach((e) => {
        const temp = {
          value: e._id,
          label: e.name + " - " + e.relation,
          color: "#00B8D9",
          isFixed: false,
        };
        nomineeOption.push(temp);
      });

      return {
        nomineeList: props.dataList.data,
        nomineeOption: nomineeOption,
      };
    }

    // Return null if the state hasn't changed
    return null;
  }
